* {
	box-sizing: border-box;
}

body {
	background-color: #ff9dc0;
	height: 100%;
	font-family: "Playfair Display";
	font-style: normal;
	font-weight: bold;
	font-size: 36px;
	line-height: 48px;
}

.container {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.top-text {
	display: flex;
	flex-direction: column;
	text-align: center;
}

.form-content {
	background: #fdff9f;
	border-radius: 10px;
}

.search-input {
	outline: none;
	border: none;
	font-family: Inconsolata;
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	line-height: 38px;
	background: #fdff9f;
	border-radius: 10px;
	margin-left: 10px;
	margin-bottom: 5px;
}

.search-btn {
	width: 55px;
	height: 52px;
	float: right;
	border-radius: 0 10px 10px 0;
	border: 0;
	cursor: pointer;
	background-color: #4aff77;
}

.search-btn:hover {
	background: #32bf55;
}

.loading {
	margin-top: 40px;
	border: 16px solid #f3f3f3;
	border-top: 16px solid #fdff9f;
	border-radius: 50%;
	width: 120px;
	height: 120px;
	animation: spin 2s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.artist-info img {
	border-radius: 50%;
	width: 100px;
	height: 100px;
	display: block;
	margin: 20px 20px auto;
}

.haiku-container {
	display: flex;
	align-items: center;
	flex-direction: column;
}

.haiku-container p {
	flex: 0 0 calc(50% - 10px);
	height: 30px;
	margin: 30px 30px 10px 10px;
}
